import React from "react";

interface CaptionsProps {
  text: string;
}

const Captions: React.FC<CaptionsProps> = ({ text }) => {
  return (
    <div style={{
      marginTop: "20px",
      fontSize: "18px",
      fontWeight: "bold",
      color: "#1cc246",
      textAlign: "center"
    }}>
      {text}
    </div>
  );
};

export default Captions;
