import React, { useEffect, useState, useRef } from "react";
import Siriwave from "react-siriwave";
import ReactHowler from "react-howler";

import Captions from "./Captions";
import "./styles.css";

export default function App() {
  const playerEl = useRef<any>(null);
  const [caption, setCaption] = useState("Initializing Jarvis...");
  const [isPlaying, setIsPlaying] = useState(false);
  const [waveAmplitude, setWaveAmplitude] = useState(0.1);

  // Fake script for Jarvis voice
  const script = [
    { time: 0, text: "Hello Sir." },
    { time: 3, text: "I am Jarvis." },
    { time: 6, text: "Your AI assistant is now online." },
  ];

  useEffect(() => {
    let timer: NodeJS.Timeout;

    if (isPlaying) {
      let i = 0;
      timer = setInterval(() => {
        if (i < script.length) {
          setCaption(script[i].text);
          i++;
        } else {
          clearInterval(timer);
        }
      }, 3000);
    }

    return () => clearInterval(timer);
  }, [isPlaying]);

  // Wave reacts to play/pause
  useEffect(() => {
    if (isPlaying) {
      const interval = setInterval(() => {
        setWaveAmplitude(Math.random() * 2 + 1); // random pulse effect
      }, 300);
      return () => clearInterval(interval);
    } else {
      setWaveAmplitude(0.1);
    }
  }, [isPlaying]);

  return (
    <div className="App">
      <Siriwave
        style="ios9"
        color="#1cc246"
        cover
        speed={0.1}
        amplitude={waveAmplitude}
        frequency={2}
        lerpSpeed={2}
      />

      <ReactHowler
        src="https://samplelib.com/lib/preview/mp3/sample-15s.mp3"
        html5
        playing={true}
        ref={playerEl}
        onPlay={() => setIsPlaying(true)}
        onEnd={() => setIsPlaying(false)}
      />

      <Captions text={caption} />
    </div>
  );
}
